export interface SelectOption {
    value: any                                                  // 
    data: Array<string>                                         // 
    private?: Boolean | Function                                // 
    reference: HTMLElement                                      // 
    popoverProps: any                                           // popover

    labelKey: string                                            // key
    valueKey: string                                            // key
    groupKey: string                                            // key
    disabledKey: string                                         // key
    iconKey: string                                             // key

    render: Function                                            // key

    autoClose: boolean                                          // 
    closeAfterBody: boolean                                     // reference  popper
    keyboard: boolean                                           // select

    onClick: Function                                           // 
    onShow: Function                                            // 
    onHide: Function                                            // 
    onClickBody: Function                                       // referencepopperbody
    onOpen: Function                                            // 
    onClose: Function                                           // 
    onItemMousedown: Function                                   // mousedown

    beforeShow: Function                                        // 
    beforeHide: Function                                        // 
}

export default class Select {

    ins = null                                                   // select service item 

    constructor(public option: SelectOption, public controller) {
        option.autoClose = option.autoClose === undefined ? true : option.autoClose
        option.closeAfterBody = option.closeAfterBody === undefined ? true : option.closeAfterBody
        option.keyboard = option.keyboard === undefined ? true : option.keyboard
    }

    /*select*/
    async show() {
        if (this.isShow()) return

        this.ins = await this.controller.getInstance()
        this.ins.bind(this)
        this.ins.show()
    }

    /*select*/
    hide() {
        if (!this.isShow()) return
        this.ins.hide()
    }

    /*/select*/
    toggle() {
        if (!!this.isShow()) {
            this.hide()
        } else {
            this.show()
        }
    }

    /**/
    prev() {
        if (!!this.ins) {
            this.ins.prev()
        }
    }

    /**/
    next() {
        if (!!this.ins) {
            this.ins.next()
        }
    }

    /**/
    selectCurrentItem() {
        if (!!this.ins) {
            this.ins.selectCurrentItem()
        }
    }

    /**/
    isShow() {
        if (!this.ins) return false
        return this.ins.isShow
    }

    /**/
    isOpen() {
        if (!this.ins) return false
        return this.ins.isOpen
    }

    destroy() {
        if (this.isShow()) {
            this.hide()
        }
        if (!!this.ins) {
            this.ins.unbind(this)
        }
    }
}